<template lang="pug">
  v-layout.mb-5(row wrap xs12 id="contact")
    h3.headline.font-ubuntu.orange--text.text--darken-3.mb-3.font-weight-bold
      | {{content.heading}}
    v-flex.px-2(xs12)
      v-card.mb-3(tile)
        v-card-title.font-ubuntu(primary-title)
          v-layout(row wrap justify-center)
            v-flex(xs12 sm9)
              v-layout(row)
                v-alert.title(v-model="form.sent"
                  dismissible
                  value="true"
                  type="success") Thank you for your message. I will reply as soon as I can.
              v-form(v-model="form.valid"
                ref="form")
                v-layout(row wrap xs12)
                  v-flex.mb-4(xs12 md6)
                    v-text-field.mx-2(v-model="form.name"
                      label="Name"
                      required)
                  v-flex.mb-4(xs12 md6)
                    v-text-field.mx-2(v-model="form.email"
                      label="E-mail"
                      required)
                  v-flex.mb-4(xs12)
                    v-text-field.mx-2(v-model="form.subject"
                      label="Subject"
                      required)
                  v-flex.mb-4(xs12)
                    v-textarea.mx-2(v-model="form.message"
                      label="Message"
                      rows="8"
                      no-resize
                      required)
                  v-flex.mb-4(xs12)
                    div.button-box
                      v-btn(color="error"
                        ripple
                        @click="clearForm()") Clear
                      v-btn(color="info"
                        ripple
                        :disabled="!form.valid"
                        @click="sendForm()") Send
</template>

<script>
import { sendMail } from "@/assets/js/mailMan";

export default {
  data() {
    return {
      content: {
        heading: "Contact"
      },
      form: {
        valid: false,
        name: "",
        // nameRules: [
        //   v => !!v || "Name is required",
        //   v => v.length >= 2 || "Name must be more than 2 characters"
        // ],
        email: "",
        // emailRules: [
        //   v => !!v || "E-mail is required",
        //   v => /.+@.+/.test(v) || "E-mail must be valid"
        // ],
        subject: "",
        // subjectRules: [
        //   v => !!v || "Subject is required",
        //   v => v.length >= 2 || "Subject must be between 2 and 40 characters"
        // ],
        message: "",
        // messageRules: [
        //   v => !!v || "Please enter your message",
        //   v => v.length >= 2 || "Message must be more than 2 characters"
        // ],
        sent: false
      }
    };
  },
  methods: {
    clearForm() {
      this.$refs.form.reset();
    },
    sendForm() {
      const formData = {
        sender: this.form.email,
        subject: this.form.subject,
        message: this.form.message
      };
      sendMail(formData);
      this.clearForm();
      document.getElementById("contact").scrollIntoView(true);
      window.scrollBy(0, -80);
      this.form.sent = true;
    }
  }
};
</script>

<style lang="sass" scoped>
.button-box
  display: flex
  justify-content: flex-end
  align-items: center
  width: 100%
</style>
