<template lang="pug">
  v-toolbar(color="light-blue"
    dark
    prominent
    fixed
    id="navbar")
    v-toolbar-title.title.font-pacifico(@click="clickMenu('home')") Willie Potgieter
    v-spacer
    v-toolbar-items.hidden-xs-only
      v-btn(flat
        tile
        ripple
        v-for="item in menu"
        :key=" 'navmenu_item_' + item.title"
        @click="clickMenu(item.title)") {{ item.title }}
    v-toolbar-side-icon.hidden-sm-and-up(@click="clickBurger()")
</template>

<script>

export default {
  methods: {
    clickBurger () {
      this.$store.dispatch('toggleSideNav', !this.sideNavState)
    },
    clickMenu (menuItem) {
      document.getElementById(menuItem).scrollIntoView(true)
      window.scrollBy(0, -80)
    }
  },
  computed: {
    menu () {
      return this.$store.getters.getMenu
    },
    sideNavState () {
      return this.$store.getters.getSideNavState
    }
  }
}
</script>

<style lang="sass" scoped>
  .title
    line-height: 3rem !important
    cursor: pointer

  #navbar
    z-index: 3
</style>
