export function sendMail (data) {
  const messageData = {
    subject: data.subject,
    sender: data.sender,
    message: data.message
  }
  axios({
    method: 'post',
    url: '/api/sendmail/PX3PN6D-6164NGC-K5AT54N-GPHSGXA',
    baseURL: 'https://webmailer.herokuapp.com',
    data: messageData
  })
}
